import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import styled from 'styled-components';
import SalarySection from './components/SalarySection';
import BonusSection from './components/BonusSection';
import BenefitsSection from './components/BenefitsSection';
import SummarySection from './components/SummarySection';
import Accordion from './components/Accordion';
import { BaseSalary, Bonus, Benefit } from './types/salary';
import { calculateSalaryDetails } from './utils/salaryCalculator';
import AdSenseBox from './components/AdSenseBox';
import SideAds from './components/SideAds';
import About from './pages/About';
import Privacy from './pages/Privacy';
import DividendCalculator from './pages/DividendCalculator';

const PageLayout = styled.div`
  display: flex;
  justify-content: center;
  align-items: flex-start;
  background: #f4f6fa;
`;

const AdColumn = styled.div`
  width: 160px;
  min-width: 120px;
  max-width: 200px;
  margin: 0 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 1100px) {
    display: none;
  }
`;

const Container = styled.div`
  max-width: 900px;
  margin: 2rem auto;
  padding: 1rem;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
`;

const Title = styled.h1`
  color: #2c3e50;
  text-align: center;
  margin-bottom: 1.5rem;
`;

const Nav = styled.nav`
  background-color: #fff;
  padding: 1rem;
  text-align: center;
  border-bottom: 1px solid #dee2e6;
`;

const NavLink = styled(Link)`
  margin: 0 1rem;
  color: #007bff;
  text-decoration: none;
  &:hover {
    color: #0056b3;
    text-decoration: underline;
  }
`;

const DownloadButton = styled.button`
  background-color: #2980b9;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  margin-left: 0.5rem;
  &:hover {
    background-color: #3498db;
  }
`;

function CalculatorApp() {
  const [salaryData, setSalaryData] = React.useState<BaseSalary>({
    basePay: 42000,
    allowances: 2200,
    mealAllowance: 3000,
    laborInsurance: 550,
    groupInsurance: 0,
    healthInsurance: 622,
    welfareFund: 285,
    laborPensionTier: 46000,
    voluntaryPensionRate: 6,
  });
  const [bonuses, setBonuses] = React.useState<Bonus[]>([
    {
      id: '1',
      name: '',
      amount: 200000,
      incomeTax: 0,
      secondGenerationNHI: 0
    },
    {
      id: '2',
      name: '1',
      amount: 61000,
      incomeTax: 0,
      secondGenerationNHI: 1287
    }
  ]);
  const [benefits, setBenefits] = React.useState<Benefit[]>([
    {
      id: '1',
      name: '',
      amount: 8000,
      isTaxable: true
    },
    {
      id: '2',
      name: '',
      amount: 20000,
      isTaxable: false
    }
  ]);

  // /
  const [allOpen, setAllOpen] = React.useState(false);
  const [salaryOpen, setSalaryOpen] = React.useState(false);
  const [bonusOpen, setBonusOpen] = React.useState(false);
  const [benefitOpen, setBenefitOpen] = React.useState(false);
  const [summaryOpen, setSummaryOpen] = React.useState(false);

  // /
  const handleExpandAll = () => {
    setAllOpen(o => !o);
    setSalaryOpen(!allOpen);
    setBonusOpen(!allOpen);
    setBenefitOpen(!allOpen);
    setSummaryOpen(!allOpen);
  };

  //  .md 
  const handleDownload = () => {
    const content = `# \n\n## \n-  (): ${salaryData.basePay?.toLocaleString() || 0} \n- // (): ${salaryData.allowances?.toLocaleString() || 0} \n-  (): ${salaryData.mealAllowance?.toLocaleString() || 0} \n- : ${salaryCalc.totalSalary.toLocaleString()} \n\n## \n- : ${salaryData.laborPensionTier?.toLocaleString() || 0} \n- : ${salaryData.voluntaryPensionRate || 0}%\n- : ${salaryCalc.employerPension?.toLocaleString() || 0} \n\n## \n- : ${salaryData.laborInsurance?.toLocaleString() || 0} \n- : ${salaryData.groupInsurance?.toLocaleString() || 0} \n- : ${salaryData.healthInsurance?.toLocaleString() || 0} \n- : ${salaryData.welfareFund?.toLocaleString() || 0} \n- : ${salaryCalc.voluntaryPension.toLocaleString()} \n- : ${salaryCalc.actualSalary.toLocaleString()} \n\n## \n${bonuses.map(b => `- ${b.name}: ${b.amount?.toLocaleString() || 0}  (: ${b.incomeTax || 0} , : ${b.secondGenerationNHI || 0} )`).join('\n')}\n\n## \n${benefits.map(b => `- ${b.name}: ${b.amount?.toLocaleString() || 0}  (${b.isTaxable ? '' : ''})`).join('\n')}`;
    const blob = new Blob([content], { type: 'text/markdown' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = '.md';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const { salaryCalc, totalCalc } = calculateSalaryDetails(salaryData, bonuses, benefits);

  return (
    <>
      <SideAds />
      <PageLayout>
        <AdColumn>
          <AdSenseBox />
        </AdColumn>
        <Container>
          <div style={{display:'flex',alignItems:'center',justifyContent:'center',marginBottom:'1.5rem'}}>
            <Title></Title>
            <button style={{marginLeft:'1rem',padding:'0.5rem 1rem',fontSize:'1rem',cursor:'pointer'}} onClick={handleExpandAll}>
              {allOpen ? '' : ''}
            </button>
            <DownloadButton onClick={handleDownload}></DownloadButton>
          </div>
          <Accordion title="" open={salaryOpen} onToggle={setSalaryOpen}>
            <SalarySection
              salaryData={salaryData}
              onChange={(field, value) => setSalaryData(prev => ({ ...prev, [field]: value }))}
              calculations={salaryCalc}
            />
          </Accordion>
          <Accordion title="" open={bonusOpen} onToggle={setBonusOpen}>
            <BonusSection
              bonuses={bonuses}
              onChange={setBonuses}
            />
          </Accordion>
          <Accordion title="" open={benefitOpen} onToggle={setBenefitOpen}>
            <BenefitsSection
              benefits={benefits}
              onChange={setBenefits}
            />
          </Accordion>
          <Accordion title="" open={summaryOpen} onToggle={setSummaryOpen}>
            <SummarySection
              calculations={totalCalc}
            />
          </Accordion>
        </Container>
        <AdColumn>
          <AdSenseBox />
        </AdColumn>
      </PageLayout>
    </>
  );
}

function App() {
  const basename = process.env.NODE_ENV === 'production' ? '/Salary-Tax-Calculator' : '';

  return (
    <Router basename={basename}>
      <header style={{backgroundColor:'#f8f9fa',padding:'1rem',textAlign:'center',borderBottom:'1px solid #dee2e6'}}>
        <h1>DinarTools - </h1>
      </header>
      <Nav>
        <NavLink to="/"></NavLink>
        <NavLink to="/dividend-calc"></NavLink>
        <NavLink to="/about"></NavLink>
        <NavLink to="/privacy"></NavLink>
      </Nav>
      <Routes>
        <Route path="/" element={<CalculatorApp />} />
        <Route path="/dividend-calc" element={<DividendCalculator />} />
        <Route path="/about" element={<About />} />
        <Route path="/privacy" element={<Privacy />} />
      </Routes>
    </Router>
  );
}

export default App; 